html {
  /* background-color: #1e1e1e; */
  /* background-color: #121212; */
  background-color: #faf9f6;
}
@media only screen and (max-width: 900px) {
  html {
    background-color: #121212;
  }
}
@media only screen and (min-width: 900px) {
  #mainIcon {
    -webkit-filter: drop-shadow(18px 28px 20px rgba(0, 0, 0, 0.25));
    filter: drop-shadow(18px 28px 20px rgba(0, 0, 0, 0.25));
  }
  #sideIcon {
    -webkit-filter: drop-shadow(0px 0px 2px rgba(0, 0, 0, 0.46));
    filter: drop-shadow(0px 0px 1px rgba(0, 0, 0, 0.46));
  }
}
.currentDay {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  color: white;
  max-width: 600px;
  max-height: 600px;
  border-radius: 40px;
  background: #1266f3;
  background: -moz-linear-gradient(45deg, #1266f3 0%, #15c3f5 100%);
  background: -webkit-linear-gradient(45deg, #1266f3 0%, #15c3f5 100%);
  background: linear-gradient(45deg, #1266f3 0%, #15c3f5 100%);
  -webkit-box-shadow: 0px 8px 18px -4px #18cbff;
  box-shadow: 0px 8px 18px -4px #18cbff;
  margin: auto;
}

#city-select {
  color: white;
}

#footer {
  color: white;
}
